import React from "react";
import styled from "styled-components";

import { Link } from "react-router-dom";

const Container = styled.div`
  width: 1230px;
  margin: auto;
  height: 45px;
`;

const Wrapper = styled.div`
  height: inherit;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const Left = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
`;

const Version = styled.span`
  font-size: 0.663rem;
`;

const Center = styled.div`
  flex: 1;
  text-align: center;
`;

const Logo = styled.h1`
  font-size: 1.163rem;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  transition: all 0.5s;
  text-decoration: underline crimson;
  &:hover {
    color: #ffa500;
    text-decoration: underline white;
    font-weight: bold;
  }
`;

const Right = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
`;

const MenuItem = styled.div`
  font-size: 0.85rem;
  font-weight: 400;
  cursor: pointer;
  margin-left: 25px;
  position: relative;
  cursor: pointer;
  color: #333;
  &:before {
    content: "";
    position: absolute;
    background-color: #ffa500;
    height: 0.8px;
    width: 0;
    bottom: -5px;
    transition: 0.5s;
    left: 50%;
    transform: translateX(-50%);
  }
  &:hover:before {
    width: 100%;
  }
`;

const Navbar = () => {
  return (
    <Container>
      <Wrapper>
        <Left>
          <Version>FR</Version>
        </Left>
        <Center>
          <Link to="/">
            <Logo>Junhan</Logo>
          </Link>
        </Center>
        <Right>
          <Link to="/job">
            <MenuItem>CurriculumVitae</MenuItem>
          </Link>
          <Link to="/contact">
            <MenuItem>Feedback</MenuItem>
          </Link>
        </Right>
      </Wrapper>
    </Container>
  );
};

export default Navbar;
