import React from "react";
import styled from "styled-components";

import Profil from "../Components/Job/Profil";
import Experience from "../Components/Job/Experience";
import Formation from "../Components/Job/Formation";
import Information from "../Components/Job/Information";

const Container = styled.div`
  width: 100%;
  height: 100%;
  margin-bottom: 20px;
`;

const Logo = styled.h2`
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 50px auto 20px auto;
  font-size: 1.5rem;
`;

const Job = () => {
  return (
    <Container>
      <Logo>Profil</Logo>
      <Profil />
      <Logo>Expériences Professionnelles</Logo>
      <Experience />
      <Logo>Formation</Logo>
      <Formation />
      <Logo>Autres informations</Logo>
      <Information />
    </Container>
  );
};

export default Job;
