import React from "react";
import "../../../Styles/formationtop.css";

const FormationTop = () => {
  return (
    <div className="FormationTop">
      <div className="headBox">
        <div className="box">
          <h1>La langue de française</h1>
          <p>
            tv5monde.com, L’Alliance française de Paris(l'école d’enseignement
            du français langue étrangère)
          </p>
          <hr />
          <h2>Période : 2018~</h2>
          <hr />
          <div className="textBox">
            <p>
              📑 J'ai appris la grammaire et la conjugaison à travers les
              journaux, le cinéma et différents thèmes.
              <br />
              📑 J'ai pratiqué l'écoute des nouvelles a la radio sur différents
              sujets.
              <br />
              📑 J'ai discuté de problèmes sociaux.
              <br />
              📑 Je m'entraîne à écouter sur TV5monde.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FormationTop;
