import styled from "styled-components";
import "../Styles/footer.css";

const Container = styled.div`
  width: 1230px;
  margin: auto;
  height: 100px;
`;

const Wrapper = styled.div`
  height: inherit;
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
`;

const Left = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
`;

const Version = styled.span`
  font-size: 0.663rem;
`;

const Center = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.85rem;
`;

const Right = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
`;

const Footer = () => {
  return (
    <Container>
      <Wrapper>
        <Left>
          <Version>Vaersion 634411</Version>
        </Left>
        <Center>Le mise à jour continue.</Center>
        <Right>
          <div className="snsContainer">
            <span>Suivez-moi</span>
            <div className="sns">
              <button
                onClick={() =>
                  window.open(
                    "https://www.linkedin.com/in/hanjun-kim-1b1741171/",
                    "_blank"
                  )
                }
              >
                <i class="fab fa-linkedin-in"></i>
              </button>
            </div>
          </div>
        </Right>
      </Wrapper>
    </Container>
  );
};

export default Footer;
